.products {
    max-width: 368px;
    &__wrapper {
        max-width: 368px;
        width: 100%;
        padding: 56px 71px 48px 73px;
        box-shadow: 0 4px 30px rgba(80, 106, 153, 0.1);
        border-radius: 32px;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    &__image {
        margin-bottom: 16px;
    }
    &__name {
        text-align: center;
        font-weight: 600;
        font-size: 18px;
        line-height: 110%;
        color: #57A6EE;
        margin-bottom: 8px;
    }
    &__descr {
        font-weight: 400;
        font-size: 16px;
        line-height: 140%;
        color: #434343;
        margin-bottom: 16px;
    }
    &__price {
        font-weight: 700;
        font-size: 24px;
        line-height: 33px;
        margin-bottom: 24px;
        color: #3F5770;
    }
    &__action {
        padding: 21px 85px;
        border: 1px solid #57A6EE;
        border-radius: 32px;
        background-color: #fff;
        font-weight: 500;
        font-size: 16px;
        line-height: 22px;
        color: #57A6EE;
    }
}

@include mq(md) {
    .products {

        max-width: 253px;

        &__wrapper {
            padding: 40px 32px 32px;
        }

        &__image {
            margin: 0 auto 16px;
            max-width: 123px;
            width: 100%;
            img {
                max-width: 100%;
            }
        }

        &__name {
            font-size: 14px;
        }

        &__descr {
            font-size: 14px;
        }

        .button {
            padding: 16px 70px;
            font-size: 14px;
        }
    }
}
