.s-maintenance-packages {
  margin: 128px 0;
  &__wrapper {
    padding: 128px 0 268px;

    background-size: cover;
    background-repeat: no-repeat;
    background-position-y: bottom;
    background-position-x: center;
  }

  &__title {
    text-align: center;
    margin: 0 auto 32px;
  }

  &__items {
    display: flex;
    gap: 33px;
    margin-bottom: 40px;
  }

  &__wrap {
    display: flex;
    gap: 114px;
    align-items: flex-end;
  }

  &__list-wrap {
    max-width: 488px;
    width: 100%;
  }

  &__text-lg {
    margin-bottom: 16px;
    display: flex;
    align-items: flex-start;
  }

  &__info {
    display: flex;
    padding: 26px 72px 26px 69px;
    border: 1px solid #FFFFFF;
    gap: 13px;
    border-radius: 24px;
  }
}

@include mq(md) {
  .s-maintenance-packages {
    margin: 72px 0;

    overflow: hidden;

    &__title {
      margin-bottom: 24px;
    }

    &__items {
      margin-bottom: 24px;
    }

    &__wrapper {
      padding: 72px 0 110px;
    }

    &__items {
      gap: 0;
    }

    &__swiper {
      overflow: initial !important;
    }

    &__wrap {
      flex-direction: column;
      gap: 12px;
    }

    &__list {
      &-wrap {
        max-width: 100%;
      }
    }

    &__info {
      padding-left: 52px;
    }
  }
}


@include mq(sm) {
  .s-maintenance-packages {
    &__wrapper {
      background-image: url("data:image/svg+xml;base64,") !important;
    }
  }
}

@include mq(xs) {
  .s-maintenance-packages {
    &__wrapper {
      padding-bottom: 96px;
    }

    &__title {
      max-width: 300px;
      width: 100%;
    }

    &__info {
      border: none;
      padding: 0;
      gap: 8px;

      &-img {
        padding-top: 2px;
        svg {
          width: 16px;
          height: 16px;
        }
      }
    }
  }
}
