.s-schedule-webinars {
  margin: 120px 0;

  &__wrapper {
    background: linear-gradient(90.53deg, #4CA3F2 0%, #80C2FF 100%);
    box-shadow: 0 4px 60px rgba(85, 118, 157, 0.15);
    border-radius: 48px;
    position: relative;
    z-index: 0;
  }

  &__inner {
    padding: 95px 97px;
    display: flex;
    justify-content: space-between;
  }

  &__img {
    position: absolute;
    height: 100%;
    width: 100%;
    z-index: -1;
    img {
      border-radius: 48px;
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
      opacity: 0.07;
    }
  }

  &__title {
    max-width: 217px;
    width: 100%;
  }

  &__text {
    max-width: 370px;
    width: 100%;
  }

  .link {
    color: $white;
    font-weight: 600;
  }
}

@include mq(md) {
  .s-schedule-webinars {
    &__wrapper {
      background: #3F5770;
      border-radius: 24px;
    }

    &--no-change-bg {
      .s-schedule-webinars {
        &__wrapper {
          background: linear-gradient(90.53deg, #4CA3F2 0%, #80C2FF 100%);
        }
      }
    }

    &__inner {
      flex-direction: column;
      align-items: center;
      text-align: center;
    }

    &__title {
      margin: 0 0 16px !important;
    }

    &__text {
      margin: 0 0 24px !important;
    }

    .button {
      margin: 0 !important;
      width: 263px;
    }

    &__button-group {
      flex-direction: column;
      gap: 16px;

      .button {
        justify-content: center;
      }
    }
  }
}

@include mq(sm) {
  .s-schedule-webinars {
    margin: 64px 0;

    &__inner {
      padding: 48px 40px;
    }
  }
}

@include mq(xs) {
  .s-schedule-webinars {
    .button {
      img {
        width: 16px;
        height: 16px;
      }
    }
  }
}
